<template>
  <div class="">
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="goodsattribute" class="demo-form-inline">
          <el-form-item label="单位名称" size="mini">
            <el-input v-model="goodsattribute.unit_name" maxlength="100" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="单位编号" size="mini">
            <el-input v-model="goodsattribute.unit_id" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="助记码" size="mini">
            <el-input v-model="goodsattribute.logogram" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="dialogFormVisible = true,addChangeType='add'" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" size="mini" @click="delFun" v-if="authority.b_del==1">删除</el-button>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border max-height="600" v-loading="loading1" :data="TableData.tableData" resizable style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="54">
            <template slot-scope="scope">
              <el-button @click="handleEdit(scope.row)" type="text" size="small">
                修改
              </el-button>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="unit_name" label="单位名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="unit_id" label="单位编号" width="120">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="logogram" label="助记码" width="100">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
      <!--添加单位修改 dialog-->
      <el-dialog :close-on-click-modal="false" title="单位管理" center :visible.sync="dialogFormVisible" width="400px" @close="closeDialog()">
        <el-form ref="unitForm" :model="unitForm" size="mini" :rules="unitRules" class="errorItem errorItemMl121">
          <el-form-item label="单位名称" prop="unit_name">
            <el-input v-model="unitForm.unit_name" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="备注" size="medium">
            <el-input type="textarea" v-model="unitForm.remark" maxlength="300" placeholder="请输入"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancelForm('unitForm')">取 消</el-button>
          <el-button type="primary" @click="submitRevision('unitForm')">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { getData } from "common/utils/public_function/commonUtils";
export default {
  name: "systemInfoSalesUnit",
  data() {
    return {
      authority: {},
      addChangeType: "add", //区分新增还是修改
      TableData: {
        tableData: [],
        per_page: [30, 50, 100, 500],
        total: 10
      },
      per_page: [30, 50, 100, 500],
      AccordingParams: {}, //参数传递值
      goPage: 1, //默认搜索页面
      pageSize: 30, //默认表格size
      loading: true,
      currentPage: 1,
      goodsattribute: {
        //查询字段
        unit_name: "",
        unit_id: "",
        logogram: ""
      },

      dialogFormVisible: false,
      multipleSelection: [], //选中行 数组集合
      dialogRevisionVisible: false, // 修改 弹框
      unitForm: {
        unit_name: "",
        remark: ""
      },
      unitRules: {
        unit_name: [
          { required: true, message: "单位名称不能为空", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "goodsattribute", { is_disabled: "" });

      this.$set(this, "AccordingParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getUnitList", //接口名
        vm: this //传递的this
      });
      getData(this.AccordingParams);
    },
    closeDialog() {
      this.unitForm = {
        unit_id: "",
        unit_name: "",
        remark: ""
      };
    },
    handleEdit(row) {
      this.addChangeType = "chenge";
      //修改计量单位  弹层
      this.dialogFormVisible = true;
      this.unitForm = {
        unit_id: row.unit_id,
        unit_name: row.unit_name,
        remark: row.remark
      };
    },
    cancelForm(unitForm) {
      //取消修改
      this.dialogFormVisible = false;
      this.$refs[unitForm].resetFields();
    },
    submitRevision(unitForm) {
      //提交修改计量单位
      var vm = this;
      this.$refs[unitForm].validate(valid => {
        if (valid) {
          if (this.addChangeType == "add") {
            var addUnitForm = {
              unit_name: this.unitForm.unit_name,
              remark: this.unitForm.remark
            };
            var funcan = {
              eachData: addUnitForm,
              UrlApi: "addUnit",
              closeDialog: (this.dialogFormVisible = false),
              list: {
                UrlApi: "getUnitList",
                data: this.TableData,
                esData: this.AccordingParams.params
                // esData: { page: this.goPage }
              }
            };
            this.$common.addAndupdateFun(funcan);
            this.addChangeType = "add";
          } else {
            var funcan = {
              eachData: this.unitForm,
              UrlApi: "editUnit",
              closeDialog: (this.dialogFormVisible = false),
              list: {
                UrlApi: "getUnitList",
                data: this.TableData,
                esData: this.AccordingParams.params
                // esData: { page: this.goPage }
              }
            };
            this.$common.addAndupdateFun(funcan);
          }
        }
      });
    },
    handleSelectionChange(val) {
      //获取当前行数据
      this.multipleSelection = val;
    },
    delFun() {
      //删除
      var deledata = {
        deleID: "unit_id",
        deleSelect: this.multipleSelection,
        deleApi: "delUnit",
        list: {
          UrlApi: "getUnitList",
          data: this.TableData
        }
      };
      this.$common.deletes(deledata, "删除", this);
    },
    onSearch() {
      //查询
      Object.assign(this.AccordingParams.params, this.goodsattribute, {
        page: 1
      });
      getData(this.AccordingParams);
    },
    checkSelectable(row) {
      return row.date !== "2016-05-01";
    },
    handleSizeChange(val) {
      Object.assign(this.AccordingParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.AccordingParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.AccordingParams.params, {
        page: val
      });
      getData(this.AccordingParams);
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "AccordingParams", {
      params: {}, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getUnitList", //接口名
      vm: this //传递的this
    });

    getData(this.AccordingParams);
  }
};
</script>

